import React, { FC } from "react";
import Slider from "react-slick";
import styled from "styled-components";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const SlideArrowNext = styled.div`
  width: 50px;
  height: 50px;
  right: 55px;
  z-index: 1;
  top: calc(50% - 20px);

  &:before {
    content: '›';
    font-family: 'Montserrat';
    background: none;
    color: #fff;
    font-size: 60px;
  }
`;

const SlideArrowPrev = styled.div`
  width: 50px;
  height: 50px;
  left: 90px;
  z-index: 1;
  top: calc(50% - 20px);

  &:before {
    content: '‹';
    font-family: 'Montserrat';
    background: none;
    color: #fff;
    font-size: 60px;
  }
`;

const SampleNextArrow = (props: any) => {
  const { className, style, onClick } = props;
  return (
    <SlideArrowNext
      className={className}
      style={style}
      onClick={onClick}
    />
  );
}

const SamplePrevArrow = (props: any) => {
  const { className, style, onClick } = props;
  return (
    <SlideArrowPrev
      className={className}
      style={style}
      onClick={onClick}
    />
  );
}

const settings = {
  infinite: true,
  speed: 500,
  centerMode: true,
  slidesToShow: 2,
  slidesToScroll: 3,
  adaptiveHeight: true,
  centerPadding: '100px',
  nextArrow: <SampleNextArrow />,
  prevArrow: <SamplePrevArrow />,
  responsive: [
    {
      breakpoint: 1280,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1,
        initialSlide: 1,
      },
    },
  ],
};

const StyledSlider = styled(Slider)`
  margin: 0;
`;

const Container = styled.div`
  margin-bottom: 155px;
`;

const SlideElement = styled.div`
  background: transparent;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 4px;
  outline: none;
  cursor: move;
`;

const Title = styled.h2`
  font-size: 4vw;
  line-height: 5vw;
  margin: 40px;
`;

const Img = styled.img`
  border-radius: 10px;
  margin-right: 30px;
  width: 100%;
  height: 100%;
`;

interface RowProps {
  title: string;
  className?: string;
  imgs: Array<{ x1: string; x2: string }>;
}

const Row: FC<RowProps> = ({ title, className, imgs }) => {
  return (
    <Container className={className}>
      <Title>{title}</Title>
      <StyledSlider {...settings}>
        {imgs.map((img) => {
          return (
            <SlideElement>
              <Img src={img.x1} loading="lazy" srcSet={`${img.x1} 1x, ${img.x2} 2x`} alt="" />
            </SlideElement>
          );
        })}
      </StyledSlider>
    </Container>
  );
};

export default Row;
