import React, { FC } from 'react';
import styled from 'styled-components';
import { MAIL } from '../../config';

const Container = styled.div`
  background: #39393952;
  padding: 56px 128px;
  display: flex;
  justify-content: space-between;

  @media (max-width: 1440px) {
    padding: 56px 20px;
  }

  @media (max-width: 1024px) {
    display: block;
  }
`;

const LeftSide = styled.div`
  text-align: center;
  margin-bottom: 60px;
`;

const RightSide = styled.div`
  display: flex;

  & .column:not(:last-child) {
    margin-right: 120px;

    @media (max-width: 1440px) {
      margin-right: 30px;
    }
  }

  @media (max-width: 1024px) {
    justify-content: center;
  }
`;

const SecondaryText = styled.p`
  opacity: 0.5;

  a {
    color: #fff;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
`;

const Link = styled.a`
  opacity: 0.5;
  cursor: pointer;
  color: inherit;
  text-decoration: none;
  margin: 22px 0;
  display: block;
`;

const TextElement = styled.span`
  letter-spacing: 0.3em;
  text-transform: uppercase;
`;

const Title = styled.h2`
  font-size: 22px;
  margin: 0;
  text-transform: uppercase;
`;

const List = styled.ul`
  list-style: none;
  padding: 0;
`;

const ColumnContainer = styled.div``;

const columns = [
  {
    title: 'Контакты',
    elements: [
      <a href="tel:+79855598929">+7 (985) 559-89-29</a>
    ],
  },
  {
    title: 'Почта',
    elements: [<a href={`mailto:${MAIL}`}>{MAIL}</a>],
  },
  {
    title: 'Навигация',
    elements: ['Главная', 'Наши работы', 'Контакты'],
    ids: ['main', 'galary', 'contacts'],
  },
];

const Footer: FC = () => {
  return (
    <Container id="contacts">
      <LeftSide>
        <TextElement>elite car delivery</TextElement>
        <SecondaryText>г.Москва, ул. Кутузовский проспект, дом 2</SecondaryText>
      </LeftSide>
      <RightSide>
        {columns.map(({ title, elements, ids }) => (
          <Column
            className="column"
            title={title}
            elements={elements}
            ids={ids}
          />
        ))}
      </RightSide>
    </Container>
  );
};

const scroll = (id: string) => (e: React.MouseEvent) => {
  e.preventDefault();
  document.getElementById(id)?.scrollIntoView({ behavior: 'smooth' });
};

interface ColumnProps {
  title: string;
  elements: React.ReactNode[];
  className: string;
  ids?: string[];
}

const Column: FC<ColumnProps> = ({ title, elements, className, ids }) => {
  return (
    <ColumnContainer className={className}>
      <Title>{title}</Title>
      <List>
        {elements.map((elemnt, index) => {
          const id = ids ? ids[index] : null;
          return (
            <li>
              {id ? (
                <Link href={`#${id}`} onClick={scroll(id)}>{elemnt}</Link>
              ) : (
                <SecondaryText>{elemnt}</SecondaryText>
              )}
            </li>
          );
        })}
      </List>
    </ColumnContainer>
  );
};

export default Footer;
