import React from "react";
import styled from "styled-components";

const Container = styled.div`
  padding-top: 100px;
  padding-left: 40px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 230px;
  padding-right: 128px;
  flex-wrap: wrap;

  @media (max-width: 1740px) {
    padding-right: 40px;
  }
`;

const NavItems = styled.div`
  display: flex;

  @media (max-width: 768px) {
    margin-top: 40px;
  }
`;

const TextElement = styled.a`
  letter-spacing: 0.3em;
  text-transform: uppercase;
  cursor: pointer;
  white-space: nowrap;
  color: inherit;
  text-decoration: none;

  &:not(:last-child) {
    margin-right: 60px;
  }

  @media (max-width: 768px) {
    font-size: 20px;
  }
`;

const scroll = (id: string) => (e: React.MouseEvent) => {
  e.preventDefault();
  document.getElementById(id)?.scrollIntoView({ behavior: 'smooth' });
};

const Navigation = () => {
  return (
    <Container>
      <TextElement>elite car delivery</TextElement>
      <NavItems>
        <TextElement href="#main" onClick={scroll("main")}>главная</TextElement>
        <TextElement href="#galary" onClick={scroll("galary")}>
          наши работы
        </TextElement>
        <TextElement href="#contacts" onClick={scroll("contacts")}>контакты</TextElement>
      </NavItems>
    </Container>
  );
};

export default Navigation;
